@import url(https://fonts.googleapis.com/css?family=Muli:400,700&display=swap);
body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

.appContainer {
  width: 80%;
  margin: 1% auto;
  font-family: 'Muli', sans-serif;

}

.App {
  text-align: center;
  /* margin: 0 4%; */
}

.todoTasksHeader {
  padding: 7px 0;
  background-color: #F0DCD7;
  margin-top: 10px;
  color: #555;


}

@media(max-width:767px) {
  .appContainer {
    width: 100%;
    margin: 0;
  }

}

.todo {
    padding: 10px;
    background-color: #8898B8;
    margin-bottom: 5px;
    /* border-bottom: 5px dotted#F0DCD7; */

}

.completed {
    padding: 10px;
    background-color: rgb(161, 185, 183);
    border-bottom: 5px solid white
}

.taskBody {
    font-size: 1.7rem;
    font-weight: bold;
    color: rgb(29, 28, 28);
    padding-bottom: 2%;
    border-bottom: 2px solid #4E567D
}

.priorityStyle {
    color: white;
    background-color: #F49B96;
    border-radius: 5px;
    padding: 0 3px;
    margin-left: 5px;

}

.taskDates {
    display: flex;
    /* justify-content: space-between */
    padding-top: 2%
}

.iconInButton {
    margin-right: 6px;


}

.startDate {
    flex-basis: 25%
}

.expiredTask {
    color: white;
    background-color: rgb(218, 54, 54);
    border-radius: 5px;
    padding: 0 3px;
    margin: 0 10px;
}

.daysLeft {
    color: white;
    background-color: #76B5B0;
    border-radius: 5px;
    padding: 0 3px;
    margin: 0 10px;
}

.btnActiveTodos {
    display: inline-flex;
    margin-top: 3%;
    margin-right: 3%;
    border: none;
    border-radius: 5px;
    padding: 3px;
    font: inherit;
    font-size: 90%;
    cursor: pointer;
}


@media (hover:hover) {
    .btnActiveTodos:hover {
        background-color: #F0DCD7
    }

}


@media (max-width:414px) {

    .taskDates {
        flex-direction: column;

    }
}
.completedTask {
    background-color: rgb(132, 121, 156)
}
.addTaskStyle {
    padding: 10px 10px 15px 10px;
    /* background-color: #76B5B0; */
    background-color: rgb(92, 101, 145)
}

.addTaskHeader {
    color: #F0DCD7;
    padding: 10px 0;
}

.inputBody {
    font-size: 1.5rem;
    width: 80%;
    border-radius: 5px;
}

.buttonsAndDate {
    margin-top: 10px;
    margin-left: 10%;
    display: flex;
    flex-wrap: wrap;

}




[value='priority'] {
    margin-right: 20px;

}

[value='deadline'] {
    margin-right: 5px;

}

[id='endDate'] {
    margin-right: 10px;
}

.btn {

    border-radius: 5px;
    border: none;

    padding: 5px 3px 3px 3px;

    display: block;
    font-size: 1rem;
    margin-top: 10px;
    cursor: pointer;
}



.btn.submit {
    background-color: #F0DCD7
}


@media (hover:hover) {

    .btn.submit:hover {
        background-color: #F49B96
    }

}

.priorityActive {

    color: black;
    background-color: #F49B96;
}

.priorityInactive {
    color: black
}
.naviContainer {
    background-color: rgb(110, 46, 31);
    margin-bottom: 10px;
    font-size: 1.4rem;
}

.headerTopContainer {
    background-color: #76B5B0;

    /* color: rgb(56, 61, 90); */
    color: white;
    padding: 1% 0 2% 4%;
    /* margin: 0 4%; */
    display: flex;
    align-items: center;
    justify-content: space-around;
    font-size: .9rem;

}

.headerTop {


    text-shadow: rgb(110, 46, 31) 3px 3px 4px;
    /* margin-bottom: 1%; */
}


.naviList {
    list-style: none;
    display: flex;
    margin-bottom: 10px;
}

.headerIcons {
    display: flex;
    flex-basis: 70%;

    justify-content: space-around;
    font-size: 2rem;
    padding-right: 20px;
    padding-left: 20px;
}



li {
    flex-grow: 1;

}

a {
    color: white;
    display: block;
    text-align: center;
    text-decoration: none;
    padding: 5px 0;
}


@media (hover:hover) {
    a:hover {
        color: #F0DCD7
    }


}




li:nth-child(1) .active {
    color: white;
}

li:nth-child(2) .active {
    color: #45BBC5;
}

li:nth-child(3) .active {
    color: #FF9100;
}

@media (max-width:375px) {

    .headerTopContainer {
        font-size: 1rem;
        flex-wrap: wrap;

    }

    .headerTop {


        padding-bottom: 3%;

    }

    .headerIcons {
        flex-basis: 100%;
        font-size: 1.5rem;
    }

    .naviContainer {
        font-size: 1rem;
    }
}
.loadMoreUsers {
    background-color: #D99982;
    padding: 15px 0;
    color: #38748C;
    font-size: 2rem;
    font-weight: bold;
    cursor: pointer;
    -webkit-transition: .2s;
    transition: .2s;
}


@media (hover:hover) {
    .loadMoreUsers:hover {
        background-color: #BF6363;
        color: #F2E1C2;

    }

}


.disclaimer {

    background-color: rgba(242, 209, 148, 0.863);
    text-align: justify;
    margin-top: 2%;
    padding: 10px 20px;
}
.shortUserContainer {
    border-bottom: 2px solid gray;
    display: flex;
    align-items: center;
    /* justify-content: space-between; */
    background-color: rgba(242, 209, 148, 0.863);
    margin-bottom: 2%;
    padding: 2% 0;
}


.imageSmall {
    margin: 0 3%;
    /* flex-grow: 1; */
    flex-basis: 10%;


}

.userShortInfo {
    flex-basis: 40%;
    padding-left: 10px;
    /* flex-wrap: wrap; */
}

.weatherContainer {
    flex-basis: 50%;
    margin-left: 5%;
    padding: 10px;
    align-self: flex-end;
    background-color: rgba(56, 116, 140, 0.89);
    color: white;
}

@media (max-width:375px) {
    .shortUserContainer {
        flex-direction: column;
    }

    .weatherContainer {
        width: 80%;
    }

    .userShortInfo {
        padding: 2% 0;
    }
}
.aboutMeContainer {
    display: flex;
    /* margin: 0 4%; */
    background-color: #76B5B0;
}

.mediaContainer {
    background-color: transparent;
    flex-basis: 40%;
    display: flex;
    flex-direction: column;
    justify-content: space-around;
}

.icons a {
    text-align: left;
    display: inline-block;
    background-color: rgba(2, 2, 2, 0.342);
    width: 5rem;
    height: 5rem;
    text-align: center;
    line-height: 5rem;
    border-radius: 50%;
}

.aboutMe {
    color: white;
    padding: 15px;
    margin: 0 5%;
    background-color: rgba(0, 0, 0, 0.253);
    text-align: justify;
    border-radius: 5px;

}


.twoFaces {

    width: 100%;
    height: 100%;
    /* padding: 5%; */


}

.image2 {
    width: 90%;
    margin: 0 5%;
    overflow: hidden;
    border-radius: 15px;
}

.icons {

    color: white;
    margin: 10% 15%;
    font-size: 3rem;
    align-items: flex-start;

}


.dialogue {
    background-color: rgba(0, 0, 0, 0.199);
    /* flex-grow: 1; */
    flex-basis: 60%;
    padding: 0 20px;
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    /* align-items: center */
}

.dialogue p {
    margin: 10px 0 5px 0;
    color: white;
    padding: 10px 10px;
    align-items: center;
    border-radius: 10px;
}

.dialogue p:nth-child(odd) {
    background-color: rgba(207, 140, 53, 0.959);
    align-self: flex-start;


}

.dialogue p:nth-child(even) {
    background-color: rgba(79, 77, 184, 0.822);
    align-self: flex-end;
    margin-left: 10%;
    text-align: right;
}

@media (max-width:415px) {

    .aboutMeContainer {
        display: flex;
        flex-direction: column;
        margin: 0;


    }

    .mediaContainer {
        flex-direction: row;
        flex-wrap: wrap;
        background-color: #76B5B0;
        display: block;
    }

    .image2 {
        margin-top: 5%;
        margin-bottom: 5%;
    }

    .icons a {
        display: none;
        /* font-size: 3rem;

        width: 4rem;
        height: 4rem;
        line-height: 4rem;
        color: rgba(255, 145, 0, 0.884); */
    }


    .dialogue {
        flex-basis: 90%;
        background-color: rgba(118, 181, 176, 0.664);

    }

    /* .aboutMe {
        order: 2;
    } */
}

@media (max-width:767px) and (orientation:landscape) {



    .mediaContainer {

        background-color: #76B5B0;

    }


    .dialogue {

        background-color: #76B5B0;

    }



}
